import React, { Fragment } from "react";

const TdHead = ({ d, i, type, mode }) =>
  type === "thead" ? (
    <Fragment>
      {mode === 'year' ? <td className="td-head">i</td> : null}
      <td className="td-head td-year">{mode === "year" ? "" : ""}</td>
      <td className="td-head"></td>
      <td className="td-head"></td>
    </Fragment>
  ) : (
    <Fragment>
      {mode === 'year' ? <td className="td-head">{i + 1}</td> : null}
      <td className="td-head td-year">
        {mode === "year" ? d[""] : d[""]}
      </td>
      <td className="td-head">{d[""]}</td>
      <td className="td-head">{d[""]}</td>
    </Fragment>
  );

export default TdHead;



